<template>
	<div>
		<div class="container center banner">
			<img src="../../static/img/banner1.webp" class="img-responsive pc-bg" />
			<img src="../../static/img/m-banner1.png" class="img-responsive m-bg" style="width: 100%;" />
		</div>
		<div class="container center info-content">
			<div class="col-lg-3 col-md-3 col-sm-12 col-xs-12">
				<!--pc-->
				<div class="pc-item-nav">
					<div class="zhaosheng">
						<a href="http://web.sgzzgz.com/zszt/#/register_guide">
							<img src="../../static/img/zhaosheng1.png" class="img-responsive" />
						</a>
						<a href="http://web.sgzzgz.com/zszt/#/school_admission_brochure">
							<img src="../../static/img/zhaosheng2.png" class="img-responsive" />
						</a>
					</div>
				</div>
				<!--mobail-->
			</div>
			<div class="col-lg-9 col-md-9 col-sm-12 col-xs-12 info">
				<div class="info-title">
					<h4>学院动态</h4>
				</div>
				<div class="news">
					<span class="h3">{{news.title}}</span>
					<div class="news-info">
						<span>时间：{{news.add_time}}</span>
						<span>来源：{{news.origin}}</span>
					</div>
					<div v-html="news.content" class="page">
						<!--<span>为了庆祝党的十九大胜利召开，营造学榜样，树榜样的校园氛围，10月18日上午9点，我院书记赵毅、副院长杨晓艳、院长助理黄捷、教师代表等集体收看中国共产党第十九次全国代表大会开幕式，认真听取习近平同志在开幕式上的报告。</span>
						<img src="../../static/img/new1.png" class="img-responsive" />
						<img src="../../static/img/new2.png" class="img-responsive" />
						<span>通过集体收看学习十九大开幕式直播， 党员干部们对我国过去五年经济社会各方面所取得的成就备受鼓舞，对习近平总书记在报告中所提到的优先发展教育等一系列政策方针热切期盼，也进一步明确了作为一名党员的历史使命和肩负责任，表示一定会把十九大的热切期盼转化为实际行动，不忘初心，牢记教育教学使命，团结奋斗，认真做好学院下一阶段发展工作，砥砺奋进！</span>-->
					</div>
				</div>
				
				<!--fenye-->
				<nav class="m-page" aria-label="...">
				  <ul class="pager">
				    <li><a href="#">&laquo;上一篇</a></li>
				    <li><a href="#">下一篇&raquo;</a></li>
				  </ul>
				</nav>
			</div>
		</div>
	</div>
</template>

<script>
export default {
	data(){
		return{
			news:'',
		}
	},
	created:function(){
    	var _self = this;
    	var newsId = this.$route.params.id;
    	document.body.scrollTop = 0;
    },
	activated:function(){
		
	  	 	 		
	},
	beforeRouteEnter (to, from, next) {
		next(vm => {
		    var _self = vm;
			var newsId = vm.$route.params.id;
//			console.log(typeof(newsId))
	　　　	var url="http://api.7171.la/api/artdetail";            
		  	vm.$http.post(url,{token:'gzsgzdzy',code:'18b304b9c103d44eca20b919dc99329e',id:newsId}
				).then(function (data) {
	    		var json = data.data;             
		  		_self.news = json;
		  		_self.$nextTick(() => {
					document.title=_self.news.title+"_韶关市中等职业技术学校 （广州校区）重点特色专业网";
				});
		  	});
	  	});
	},
    watch: {
        '$route': function () {
            this.newsId = this.$route.params.id;
        }
    },
	activated(){
		 document.documentElement.scrollTop = 0
	}
}
</script>

<style>
	.page span{
		display: initial;
	}
</style>


