<template>
  <div id="header">
  	<div class="pc_top">
			<div class="container">
				<div class="logo">
					<router-link to="/">
						<img src="../../static/img/logo.png"/>
					</router-link>
				</div>
				<div class="top_info">
					<div>
						<!--<span class="active">学院微信<div><span></span><img src="../../static/img/qcore.png" /></div></span>
						<div></div>-->
						<i class="iconfont icon-zhengfangxing"></i><span>国家重点公办中职学校</span>
					</div>
					<div>
						<i class="iconfont icon-zhengfangxing"></i><span>国家级示范性中职学校</span>
					</div>
					<div>
						<i class="iconfont icon-zhengfangxing"></i><span>国家职业技能鉴定所</span>
					</div>
				</div>
			</div>
		</div>
    <nav class="pc_nav">
			<div class="container">
				<ul class="list-inline first-nav">
					<li @click="selected(item.name,item.mca)" :class="{active: activeName == item.name || (uri == '/' && index == 0 )}" ref="li" v-for="(item, index) in navList" v-if="index != 5">
						<router-link to="/" class="text-center">{{item.name}}</router-link>
						<ul class="second-nav list-unstyled">
							<li v-for="item1 in item._data" @click.stop="selected(item1.name,item1.mca)" >
								<a class="text-center">{{item1.name}}</a>
							</li>
						</ul>	
					</li>
					<li v-else>
						<a class="text-center" href="http://web.sgzzgz.com/zszt" >{{item.name}}</a>
					</li>
				</ul>	
			</div>
		</nav>
		<!--mobail-->
		<div class="m-top">
			<router-link to="/">
				<img src="../../static/img/m-logo1.png" />
			</router-link>
			<img src="../../static/img/nav.png" class="m-nav-botton" data="close"/>
		</div>
		<div class="m-logo">
			<img src="../../static/img/m-logo.png"/>
		</div>
		<nav class="m-nav" id="nav">
			<ul>
				<li @click="selected(item.name,item.mca,'mobail')"  v-for="(item,index) in navList" v-if="index != 5">
					<a>
						<i class="iconfont" :class="item.ico"></i><span>{{item.name}}</span>
					</a>	
						<ul class="m-second-nav list-unstyled" :class="{active: activeName == item.name }">
							<li v-for="item1 in item._data" @click.stop="selected(item1.name,item1.mca,'child')" ><a><span>{{item1.name}}</span></a></li>
						</ul>
				</li>
				<li v-else>
					<a href="http://web.sgzzgz.com/zszt" ><i class="iconfont" :class="item.ico"></i><span>{{item.name}}</span></a>
				</li>
			</ul>
		</nav>
  </div>
</template>

<script>
	export default {
		data(){
        return{
        	isShow: false,
        	navList: [],
        	gameName:'',
          activeName: '', 
          status: false,
          uri: ''
        }
    },
    created:function(){
    	var _self = this;
    	_self.uri = this.$route.path;
　　　	var url="http://api.7171.la/api/menu";            
		  this.$http.post(url,{token:'gzsgzdzy',code:'18b304b9c103d44eca20b919dc99329e'}
			).then(function (data) {
        var json = data.data;
		  	_self.navList = json;
		  	_self.navList = _self.navList[0]._data;
		  	_self.$nextTick(() => {
					_self.address(_self.uri);			
				});
    	});      
    },
    watch: {
    	$route (to, from) {
    		this.uri = this.$route.path;
    		this.address(this.uri);
    	}	
    },
		methods: {
	    selected(name,url,type) {
	    	$(".first-nav li").removeClass("active");
	      this.activeName = name;
      	this.$router.push({ path: url });
	      if(type == "mobail"){
	      		$(".m-nav").removeClass("m-nav-move");
						$("body").removeClass("body");
	      }
	    },
	    address(url){
	    	if( url == "/"){
    			document.title = "韶关市中等职业技术学校 （广州校区）重点特色专业网";
    			$(".first-nav li").removeClass("active");
    			$(".first-nav li").eq(0).addClass("active"); 
    		}
	    	if( url == "/contact"){
    			$(".first-nav li").removeClass("active");
    			$(".first-nav li").eq(8).addClass("active"); 
    		}
	    	if( url == "/sc_info"){
    			$(".first-nav li").removeClass("active");
    			$(".first-nav li").eq(1).addClass("active"); 
    		}
    		if( url == "/teachers1" || url == "/teachers" || url == "/teachers3"){
    			$(".first-nav li").removeClass("active");
    			$(".first-nav li").eq(3).addClass("active"); 
    		}
    		if(url.indexOf("news_page") >= 0 || url == "/sc_dynamics") { 
    				$(".first-nav li").removeClass("active");
				    $(".first-nav li").eq(2).addClass("active"); 
				}
    		if(url.indexOf("news_page1") >= 0 || url == "/special_construction") { 
    				$(".first-nav li").removeClass("active");
				    $(".first-nav li").eq(4).addClass("active"); 
				}
    		if(url.indexOf("news_page2") >= 0 || url == "/employment_practice") {
    				$(".first-nav li").removeClass("active");
				    $(".first-nav li").eq(6).addClass("active"); 
				}
    		if(url.indexOf("news_page3") >= 0 || url == "/student_activities") {
    				$(".first-nav li").removeClass("active");
				    $(".first-nav li").eq(7).addClass("active"); 
				}
	    }
		}
	}	
</script>





