<template>
	<div>
		<!--carousel-->
		<div class="container device center">
			<div class="swiper-container lunbo">
		        <div class="swiper-wrapper">
		           
		            <div class="swiper-slide" v-for="item in lunList">
		            	<a href="#">
			            	<img :src="item.pic" class="img-responsive">
		            	</a>
		            </div>
		            
		        </div>
		        <!-- Add Pagination -->
		        <div class="pagination">
		        	<!--swiper-pagination-bullet-->
		        	<div>
		        		<h5>我院隆重举行2017届毕业典礼，家长师生酒店界...</h5>
		        	</div>
			    	<div class="index">
			    		<span class="text-center swiper-pagination-switch" @click="forward('1')" v-for="(item,index) in lunList" :class="{'active':!index}">
			    			{{index+1}}
			    		</span>
			    	</div>
		        </div>
		        <!-- Add Arrows -->
		        <div class="swiper-button-next lunbo-next">
		        	<i class="iconfont icon-right"></i>
		        </div>
		        <div class="swiper-button-prev lunbo-prev">
		        	<i class="iconfont icon-back"></i>
		        </div>
		    </div>
		</div>
		
		<!--main-->
		<div class="container center box1">
			<div class="col-lg-6 col-md-6 col-sm-12 col-xs-12">
				<h4>学校动态</h4>
				<div class="more">
					<router-link to="/sc_dynamics">
						<i class="icon"></i><span>更多</span>
					</router-link>
				</div>
				<hr />
				<div class="news-box" v-for="item in dynList">
					<div class="date">
						<h3 class="text-center">{{item.day}}</h3>
						<span class="text-center">{{item.year}}/{{item.mouth}}</span>
					</div>
					<div class="news-title">
						<router-link :to="{name: 'news', params: { id: item.id }}" target="_blank">
							<p>{{item.title}}</p>
							<span>{{item.synopsis}}</span>
						</router-link>
					</div>
				</div>
			</div> 
			<div class="col-lg-6 col-md-6 col-sm-12 col-xs-12">
				<h4>专业建设</h4>
				<div class="more">
					<router-link to="/special_construction">
						<i class="icon"></i><span>更多</span>
					</router-link>
				</div>
				<hr />
				<div class="news-box1">
					<div v-for="item in noticeList">
						<router-link :to="{name: 'news1', params: { id: item.id }}" target="_blank">
							<i class="iconfont icon-zhengfangxing"></i>
							<span>{{item.title}}</span>
							<p>{{item.add_time}}</p>
						</router-link>
					</div>
				</div>
			</div>
		</div>
		<div class="container center box2">
			<div>
				<h4>就业实习</h4>
				<div class="more">
					<router-link to="/employment_practice">
						<i class="icon"></i><span>更多</span>
					</router-link>
				</div>
				<hr />
				<div class="news-box1">
					<div v-for="item in empList">
						<router-link :to="{name: 'news2', params: { id: item.id }}" target="_blank">
							<i class="iconfont icon-zhengfangxing"></i>
							<span>{{item.title}}</span>
							<p>{{item.add_time}}</p>
						</router-link>
					</div>
				</div>
			</div>
			<div>
				<h4>学生活动</h4>
				<div class="more">
					<router-link to="/student_activities">
						<i class="icon"></i><span>更多</span>
					</router-link>
				</div>
				<hr />
				<div class="news-box1">
					<div v-for="item in stuList">
						<router-link :to="{name: 'news3', params: { id: item.id }}" target="_blank">
							<i class="iconfont icon-zhengfangxing"></i>
							<span>{{item.title}}</span>
							<p>{{item.add_time}}</p>
						</router-link>
					</div>
				</div>
			</div>
			<div>
				<h4>师资队伍</h4>
				<div class="more">
					<router-link to="/teachers1">
						<i class="icon"></i><span>更多</span>
					</router-link>
				</div>
				<hr />
				<div class="swiper-container shizi">
			        <div class="swiper-wrapper">
			            <div class="swiper-slide" v-for="item in teacherList">
			            	<img :src="item.headimgtwo" class="img-responsive" />
			            </div>
			        </div>
			        <!-- Add Arrows -->
			        <div class="swiper-button-next carousel-control"><i class="iconfont icon-right"></i></div>
			        <div class="swiper-button-prev carousel-control"><i class="iconfont icon-back"></i></div>
				</div>
			</div>
		</div>
		<div class="center">
			<img src="../../static/img/bg10.png" class="img-responsive pc-bg"/>
			<img src="../../static/img/m-bg10.png" class="img-responsive m-bg" style="width: 100%;"/>
		</div>
		
		<div class="container center box1">
			<h4 class="">校园环境</h4>
			<div class="more">
				<a href="javascript:void(0);" @click="test(1)">
					<i class="icon"></i><span>更多</span>
				</a>
			</div>
			<hr />
			<div class="pic-box1">
				<div v-for="(item, index) in styleList" @click="test(index+1)">
					<a href="javascript:void(0);">
						<img v-lazy="item.img" class="img-responsive" />
						<!--<div class="pic-shadow">
							<span class="text-center">{{item.text}}</span>
						</div>-->
					</a>
				</div>
			</div>
		</div>	
		
		<div class="container center coop">
			<div class="coop-title">
				<h4 class="text-center">常用链接</h4>
			</div>
		</div>
		
		<div class="container center " style="padding: 0;">
			<div class="link">
				<div v-for="item in linkList">
					<a href="javascript:void(0);">
						<img v-lazy="item.img" class="img-responsive" />
					</a>
				</div>
			</div>
		</div>
		<div class="pic-shadow1">
			<div class="wrap container">
				<div id="sildes">
			    <div class="control">
			      <ul class="change">
			      </ul>
			    </div>
			    <div class="thumbWrap">
			      <div class="thumbCont">
			        <ul>
			          <!-- img属性, url=url, text=描述, bigimg=大图, alt=标题  -->
			          <li>
			            <div><img src="../../static/img/thumb1.png" url="#" text="" bigImg="../../static/img/big1.png" alt=""></div>
			          </li>
			          <li>
			            <div><img src="../../static/img/thumb2.png" url="#" text="" bigImg="../../static/img/big2.png" alt=""></div>
			          </li>
			          <li>
			            <div><img src="../../static/img/thumb3.png" url="#" text="" bigImg="../../static/img/big3.png" alt=""></div>
			          </li>
			          <li>
			            <div><img src="../../static/img/thumb4.png" url="#" text="" bigImg="../../static/img/big4.png" alt=""></div>
			          </li>
			          <li>
			            <div><img src="../../static/img/thumb5.png" url="#" text="" bigImg="../../static/img/big5.png" alt=""></div>
			          </li>
			          <li>
			            <div><img src="../../static/img/thumb6.png" url="#" text="" bigImg="../../static/img/big6.png" alt=""></div>
			          </li>
			          <li>
			            <div><img src="../../static/img/thumb7.png" url="#" text="" bigImg="../../static/img/big7.png" alt=""></div>
			          </li>
			          <li>
			            <div><img src="../../static/img/thumb8.png" url="#" text="" bigImg="../../static/img/big8.png" alt=""></div>
			          </li>
			        </ul>
			      </div>
			    </div>
			  </div>
			</div>
		</div>
	</div>
</template>
<script>
import Swiper from 'Swiper';
import {olvSlides} from '../../static/js/slides-1.1.1-min.js' //注意路径
import {dotdotdot} from '../../static/js/jquery.dotdotdot.js'
var swiper,hList=[],g=0;
export default {
	data(){
        return{
        	totalList: [],
        	//学院动态
        	dynList:[],
        	//gongjian
        	noticeList:[],
        	//employment,
        	empList:[],
        	//student
        	stuList:[],
        	lunList:[],
        	hList:[],
        	teacherList:[],
        	styleList:[
        		{ img : "../../static/img/school1.png"},
        		{ img : "../../static/img/school2.png"},
        		{ img : "../../static/img/school3.png"},
        		{ img : "../../static/img/school4.png"},
        	],
        	linkList:[
        		{ img:"../../static/img/link1.png"},
        		{ img:"../../static/img/link2.png"},
        		{ img:"../../static/img/link3.png"},
        		{ img:"../../static/img/link4.png"},
        	],
        	
        }
    },
    mounted() {
    	
    },
	created:function(){
    	var _self = this;
　　　	var url="http://api.7171.la/api/article";            
		  this.$http.post(url,{token:'gzsgzdzy',code:'18b304b9c103d44eca20b919dc99329e',type:'17,19,20,21'}
			).then(function (data) {
       		var json = data.data;
		  	_self.totalList = json;
			for (var i = 0;i<_self.totalList.length;i++) {
				if(_self.totalList[i].type == "17"){
					if(_self.dynList.length<3){
						_self.dynList.push(_self.totalList[i]);
						var dateTime = _self.totalList[i].add_time;
						dateTime =dateTime.split("-");
						_self.totalList[i].day = dateTime[2];
						_self.totalList[i].mouth = dateTime[1];
						_self.totalList[i].year = dateTime[0];
					}
				}
				if(_self.totalList[i].type == "19"){
					if(_self.noticeList.length<7){
						_self.noticeList.push(_self.totalList[i]);
					}
				}
				if(_self.totalList[i].type == "20"){
					if(_self.empList.length<6){
						_self.empList.push(_self.totalList[i]);
					}
				}
				if(_self.totalList[i].type == "21"){
					if(_self.stuList.length<6){
						_self.stuList.push(_self.totalList[i]);
					}
				}
				_self.$nextTick(() => {
					$('.news-title span').dotdotdot();
				});
			}
    	});
    	var url1="http://api.7171.la/api/banner";            
		  this.$http.post(url1,{token:'gzsgzdzy',code:'18b304b9c103d44eca20b919dc99329e'}
			).then(function (data) {
        	var json = data.data;
		  	_self.lunList = json;
			for(var i=0;i<_self.lunList.length;i++){
				hList.push(_self.lunList[i].title);
			}
    	});     
    	var url2="http://api.7171.la/api/teacher";            
		  this.$http.post(url2,{token:'gzsgzdzy',code:'18b304b9c103d44eca20b919dc99329e'}
			).then(function (data) {
        	var json = data.data;
		  	//_self.lunList = json;
		  	var z,n,m=0;
			for(var i=0;i<json.length;i++){
				if((json[i].type == 9) ){
					z++;
					_self.teacherList.push(json[i])
				}
				if(json[i].type == 10 && n <2){
					n++;
					_self.teacherList.push(json[i])
				}
				if(json[i].type == 11 && m <2){
					m++;
					_self.teacherList.push(json[i])
				}
			}
			_self.$nextTick(() => {
				_self.lunbo();				
			});
    	});
    },
    methods: {
	    forward: function(n) {
	    	swiper.slideTo(n, 1000, false);
	    },
	    lunbo(){
	    	function active(n){
		  	 	$(".index").find("span").removeClass("active");
		  	 	$(".index").find("span").eq(n).addClass("active");
		  	 	$(".pagination h5").text(hList[n]);
		  	}
	    	swiper = new Swiper('.lunbo', {
	            pagination: '.swiper-pagination',
		        paginationClickable: true,
		        nextButton: '.swiper-button-next',
        		prevButton: '.swiper-button-prev',
		        loop:'true',
		        lazyLoading : true,
		        autoplay : 6000,
	            onTouchEnd: function(swiper){
			      active(swiper.realIndex);
			    },
			    onSlideChangeEnd: function(swiper){
				    active(swiper.realIndex);
			    }
	        });
	        $(".lunbo-next,.lunbo-prev").click(function(){
	        	active(swiper.realIndex);
	        });
	        $(".pagination span").click(function(){
		  	 	$(".index").find("span").removeClass("active");
		  	 	var num = $(this).index();
		  	 	swiper.slideTo(num+1, 1000, false);
		  	 	active(num);
		  	});
		  	var swiper1 = new Swiper('.shizi', {
				effect: 'coverflow',
                loop: 'true',
                speed: 600,
                initialSlide :1,
                autoplay: 4000,
                /*coverflow: {
				    rotate: 0,// 旋转的角度  
				    stretch: 250,// 拉伸   图片间左右的间距和密集度  
				    depth: 250,// 深度   切换图片间上下的间距和密集度  
				    modifier: 1,// 修正值 该值越大前面的效果越明显  
				    slideShadows : false// 页面阴影效果  
				  },*/
		        nextButton: '.swiper-button-next',
    			prevButton: '.swiper-button-prev',
                
            });
	    },
	    test(id){
    		//console.log(id);
    		g++;
    		if( g==1 ){
    			$('#sildes').olvSlides(
					{
						thumb:true,
						thumbPage:true,
						thumbDirection:"Y",
						effect:'fade',
						start: 1,
						stop:false
					}
				);
    		}else{
    			$(".pic-shadow1").css("display","block");
    		}
    	},
	    select(uri){
	    	this.$router.push({ path: uri });
	    },
	    activated(){
	    	document.title = "韶关市中等职业技术学院 （广州校区）重点特色专业网";
	    	document.documentElement.scrollTop = 0
	    },
	}
}
</script>

<style>
	img[lazy=loading] {
	    /*width: 100px;*/
	    background-position:center center!important;
	    background: url("../../static/img/spinner.svg");
	    background-size: 65px 65px !important;
	    background-repeat:no-repeat;
	    -webkit-background-size: cover;
	    -moz-background-size: cover;
	    -o-background-size: cover;
	    background-size: cover;
	    height: 131px;
        margin: 0 auto;
	}
	.shizi img{
		margin: 0 auto;
	}
	.shizi .swiper-button-next,.shizi .swiper-button-prev{
		background:transparent;
		opacity: .9;
	    width: 40px;
	}
	.shizi .swiper-button-next i,.shizi .swiper-button-prev i{
		color: #087d4f;
	    display: block;
	    font-size: 40px;
	    font-weight: bolder;
	}
	.pic-shadow1{
		background-color:rgba(0,0,0,0.8);
	    position: absolute;
	    top: 0;
	    height: 100%;
	    width: 100%;
	    z-index: 2;
	    display: none;
	}
	.wrap{
	    position: fixed;
	    left: 50%;
	    top: 20%;
	    z-index: 3;
	    transform: translateX(-50%);
	    -webkit-transform: translateX(-50%);
	    -moz-transform: translateX(-50%);
	    -o-transform: translateX(-50%);
	    -mo-transform: translateX(-50%);
	}
	@media only screen and (max-width: 980px) {
		.change li{
			width: 570px;
		}
	}
	@media only screen and (max-width: 768px) {
		.wrap{
			width: 100%;
		}
	}
	@media only screen and (max-width: 680px) {
		.change li {
		    width: 520px;
		}
	}
	@media only screen and (max-width: 580px) {
		.change li {
		    width: 440px;
		}
		.change li .imgWrap,.control,.thumbWrap{
			height: 440px;
		}
		.thumbNext{
			top: 410px;
		}
	}
	@media only screen and (max-width: 480px) {
		.wrap{
			top: 15%;
		}
		.thumbWrap{
			display: none !important;
		}
		.control{
			width: 100% !important;
			height: 400px;
		}
		.change li .imgWrap{
			height: 400px;
		}
	}
	@media only screen and (max-width: 414px) {
		.change li .imgWrap,.control {
		    height: 350px;
		}
		.change li {
		    width: 380px;
		}
		.wrap {
		    top: 25%;
		}
	}
	@media only screen and (max-width: 375px) {
		.next,.prev{
		    background-position:-5px -3px;
		    width: 40px;
		    height: 40px;
		}
		.change li {
		    width: 345px;
		}
	}
	@media only screen and (max-width: 360px) {
		.change li {
		    width: 330px;
		}
		.control ,.change li .imgWrap{
		    height: 350px;
		}
		.control, .change li .imgWrap {
		    height: 300px;
		}
	}
	@media only screen and (max-width: 320px) {
		.wrap {
		    padding: 0;
		}
		.change li {
		    width: 320px;
		}
	}
</style>


